.footer {
  display: flex;
  justify-content: space-between;
  padding: 15px 30px;
  border-radius: 20px 20px 0 0;
  box-shadow: 0px 0px 15px $blue;
  background-color: $green;
  clear: both;
  position: relative;
  height: 50px;
  margin-top: -50px;
  color: $white;
  text-shadow: 1px 1px 2px $black;
  font-size: 20px;
}

.fa-linkedin {
  text-decoration: none;
  color: $white;
}
